#functional-buttons button {
  width: 100%;
}
#functional-buttons .btn {
  padding: 12px 16px;
  cursor: pointer;
  border-width: 1px;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 500;
  -webkit-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  -moz-box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  box-shadow: 0px 10px 20px -6px rgba(0, 0, 0, 0.12);
  overflow: hidden;
  position: relative;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
#functional-buttons .btn:hover span, 
#functional-buttons .btn:active span, .btn:focus span {
  margin-left: -10px;
}

#functional-buttons .btn.btn-primary {
  background: #ec8f6a;
    border-color: #ec8f6a;
  color: #fff;
}
#functional-buttons .btn.btn-secondary {
  color: #fff;
  border-color: #8bbabb;
  background: #8bbabb;
}
#functional-buttons .btn.btn-tertiary {
  color: #fff;
  background: #e2c275;
  border-color: #e2c275;
}
#functional-buttons .btn.btn-quarternary {
  color: #fff;
  border-color: #b96b9f;
  background: #b96b9f;
}
#functional-buttons .btn.btn-primary:hover, 
.btn.btn-primary:focus {
  border-color: #e66d3d !important;
  background: #e66d3d !important;
  opacity: 1;
  scale: 1 !important;
  transition: all 200ms ease-in-out;
}
#functional-buttons .btn.btn-secondary:hover, 
.btn.btn-secondary:focus {
  border-color: #6ba7a8 !important;
  background: #6ba7a8 !important;
  opacity: 1;
  scale: 1 !important;
  transition: all 200ms ease-in-out;
}
#functional-buttons .btn.btn-tertiary:hover, 
.btn.btn-tertiary:focus {
    border-color: #d9af4b !important;
    background: #d9af4b !important;
    opacity: 1;
    scale: 1 !important;
    transition: all 200ms ease-in-out;
}
#functional-buttons .btn.btn-quarternary:hover, 
.btn.btn-quarternary:focus {
  border-color: #a44d87 !important;
  background: #a44d87 !important;
  opacity: 1;
  scale: 1 !important;
  transition: all 500ms ease-in-out;
}

#functional-buttons .btn span {
  margin-left: -20px;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

#functional-buttons .btn .icon {
  position: absolute;
  top: 0;
  right: 0;
  width: 45px;
  bottom: 0;
  background: #fff;
}

#functional-buttons .btn .icon i {
  font-size: 20px;
}
#functional-buttons .btn .icon i:hover,
#functional-buttons .btn .icon i:focus {
  opacity: 1;
}
#functional-buttons .btn.btn-primary .icon i {
  color: #ec8f6a;
}
#functional-buttons .btn.btn-secondary .icon i {
  color: #8bbabb;
}
#functional-buttons .btn.btn-tertiary .icon i {
  color: #e2c275;
}
#functional-buttons .btn.btn-quarternary .icon i {
  color: #b96b9f;
}

.cite-buttons#functional-buttons button {
  width: auto;
}

