@import url('https://fonts.googleapis.com/css2?family=Albert+Sans:ital,wght@0,100..900;1,100..900&display=swap');

#literary-device-finder {
    padding-top: 100px;
    padding-bottom: 100px;
    font-family: "Albert Sans", sans-serif;
    font-weight: 400;
    color: #1d201f;
}

h2 {
    font-size: clamp(30.693px, 1.918rem + ((1vw - 3.2px) * 3.642), 45px);
    font-weight: 700;
}

h4 {
    font-size: 25px;
    padding-top: 10px;
    padding-bottom: 10px;
}

h5 {
    font-size: 30px;
    font-weight: 600;
    padding-bottom: 20px;
}

#excerpt input,
#title input,
#title select {
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 25px;
}

input,
select,
textarea {
    border-color: #86b7fe;
    outline: 0;
    box-shadow: 0 0 0 .25rem rgba(13, 110, 253, .25);
}

input:focus,
select:focus,
textarea:focus {
    border-color: #6698e3;
    box-shadow: 0 0 0 .5rem rgba(13, 110, 253, .25);
} 

#title select {
    padding-left: 20px;
    padding-top: 16px;
    padding-bottom: 16px;
}


#excerpt .buttons-group {
    text-align: left;
}

#excerpt .buttons-group button {
    margin: 10px;
}
#excerpt .buttons-group button:first-child {
    margin-left: 0;
}

#search {
    margin-top: 10px;
    margin-bottom: 10px;
}

#functional-buttons button {
    margin: 10px;
}

#share {
    padding-top: 40px;
}

#enter-button {
    background-color: transparent;
    border: none!important;
    float: right;
}

#enter-button img {
    width: 45px;
    height: auto;
    /* position: relative;
    top: -57px;
    left: -6px; */
    margin-top: -93px;
    margin-right: 10px;
}
#enter-button img:hover {
    scale: 1.2;
    transform: rotateX('angle');
    cursor: pointer;
    transition-duration: 500ms;
}

#send-icon-2 {
    margin-top: -80px !important;
    margin-right: 12px !important;
}

#excerpt textarea::placeholder {
    font-size: 25px;
}


/* Dropdown */
#dropdown select {
    text-align: center;
    margin-right: calc(50% - 2px);
}

#dropdown img {
    display: inline;
    float: right;
    position: relative;
    width: 40px;
    height: auto;
    top: -53px;
    left: 0px;
}

#dropdown img.dropdown2 {
    float: left;
    left: 10px;
}

/* Preloader */
#preloader svg {
    height: 156px;
    width: auto;
}

circle[Attributes] {
    fill: rgb(254, 1, 119);
    stroke: rgb(254, 1, 119);
    stroke-width: 7;
}

/* Functional buttons */
#clear {
    /* background-color: #1146d6;
    border-color: #1146d6; */
    color: white;
    transition: all 500ms ease-in-out;
}

#download {
    /* background-color: #d9bb11;
    border-color: #d9bb11; */
    color: white;
    transition: all 500ms ease-in-out;
}

#copy {
    /* background-color: #5eb433;
    border-color: #5eb433; */
    color: white;
    transition: all 500ms ease-in-out;
}

#cite {
    /* background-color: #b243d9;
    border-color: #b243d9; */
    color: white;
    transition: all 500ms ease-in-out;
}

#clear:hover,
#download:hover,
#copy:hover,
#cite:hover {
    opacity: 0.7;
    transition: opacity .15s ease-in-out;
    scale: 1.1;
    transition: all 500ms ease-in-out;
}

/* Social buttons */
.btn-fb,
.btn-tw,
.btn-in,
.btn-gp {
    color: white;
}

.btn-fb:hover {
    background-color: #1f335f !important;
    color: white;
    opacity: 0,9;
}
.btn-tw:hover {
    background-color: #056fb2 !important;
    color: white;
    opacity: 0,9;
}
.btn-in:hover {
    background-color: #015582 !important;
    color: white;
    opacity: 0,9;
}
.btn-gp:hover {
    background-color: #315196 !important;
    color: white !important;
    opacity: 0,9;
}

/* Result */
.result-place img {
    width: auto;
    height: 250px;
    padding-top: 30px;
}

#result {
    text-align: left;
    font-size: 22px;
    padding-top: 30px;
    font-weight: 400;
    padding-bottom: 30px;
}

/* Popup */
.popup {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    animation: fadeOut 2s ease-in-out forwards;
  }

  /* TABLE */
  table {
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 20px;
    margin-top: 20px;
    /* border-radius: .375rem; */
    table-layout: auto;
    text-align: left;
  }

  thead {
    border-bottom-color: #d1d5db;
    border-bottom-width: 1px;   
  }
  
  th, td {
    border: 1px solid #d1d5db;
    padding: 8px;
  }
  
th {
    background-color: #e5e7eb;
    border-bottom-width: 1px;
    border-left-width: 1px;
    border-top-width: 1px;
    padding: .65rem .75rem;
}
tbody {
    display: table-row-group;
    vertical-align: middle;
    unicode-bidi: isolate;
}

tr {
    border-bottom-width: 0;
    border-bottom-color: #e5e7eb !important;
}

tbody tr:last-child td:first-child {
    /* border-bottom-left-radius: .375rem; */
}

td:last-child {
    border-right-width: 1px;
}

td {
    border-bottom-width: 1px;
    border-color: rgb(0 0 0 / 15%);
    border-left-width: 1px;
    padding: .65rem .75rem;
}

th:first-child {
    /* border-top-left-radius: .375rem; */
}
th:last-child {
    border-right-width: 1px;
    /* border-top-right-radius: .375rem; */
}
  
  tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  tr:hover {
    background-color: #ddd;
  }
  
  td:first-child {
    font-weight: bold;
  }
  
  td:nth-child(2) {
    /* width: 40%; */
  }
  
  td:nth-child(3) {
    width: 50%;
  }
  
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

/* Citiation */
#citiation p {
    text-align: center;
    font-size: 20px;
    padding-top: 0;
    margin-top: 0;
    padding-bottom: 10px;
}

#citiation a {
    text-decoration: none;
}



/* MEDIA QUERY */
/* Laptop */
@media only screen and (max-width: 1024px) {
    #dropdown1 {
        padding-bottom: 30px;
    }
}

/* Tablet */
@media only screen and (max-width: 768px) { 
}

/* Mobile */
@media only screen and (max-width: 525px) {
    #literary-device-finder {
        padding-top: 40px;
        padding-bottom: 40px;
        font-family: "Albert Sans", sans-serif;
        font-weight: 400;
    }
    h2 {
        font-size: 28px;
    }
    h4 {
        font-size: 20px;
    }
    h5 {
        font-size: 28px;
    }
    #excerpt input, 
    #title input, 
    #title select {
        padding-top:    15px;
        padding-bottom: 15px;
        font-size:      17px;
    }
    #enter-button img {
        width: 38px;
        margin-top: -88px;
        margin-right: 1px;
    }
    #title {
        text-align: center;
    }
    #text-input {
        margin-top: -15px;
    }
    #dropdown img {
        top: -47px;
    }
    #functional-buttons button {
        margin: 4px;
        font-size: 15px;
        width: 100%;
    }
    .optionbox:before {
        line-height: inherit !important;
        /* right: 37px !important; */
    }
    .result-place img {
        padding-top: 1px;
    }
    #result table {
        margin-top: 5px;
        margin-bottom: 5px;
        font-size: 13px;
    }
    #result th,
    #result tr {
        padding: .15rem .35rem;
    }
    #share {
        padding-top: 4px;
    }
}

@media only screen and (max-width: 425px) {}

@media only screen and (max-width: 375px) {
    .optionbox:before {
        line-height: inherit !important;
        /* right: 4px !important; */
    }
 }

@media only screen and (max-width: 320px) {
    .optionbox select {
        width: 164px;
    }
    .optionbox:before {
        /* right: 22px !important; */
    }

    #result th:first-child,
    #result td:first-child { 
    }
 }
