.container {
    text-align: center;
  }
  
  /* Solid Social Share Buttons */
  
  .btn-social,
  .btn-social:visited,
  .btn-social:focus,
  .btn-social:hover,
  .btn-social:active {
    color: #ffffff;
    text-decoration: none;
    transition: opacity .15s ease-in-out;
  }
  
  .btn-social:hover,
  .btn-social:active {
   opacity: .75 !important;
   color: inherit !important;
   background-color: inherit !important;
   border: inherit !important;
  }
  
  .btn-fb {
    background-color: #3b5998;
  }
  
  .btn-tw {
    background-color: #1da1f2;
  }
  
  .btn-in {
    background-color: #0077b5;
  }
  
  .btn-gp {
    background-color: #db4437;
  }
  
  .btn-rd {
    background-color: #ff4500;
  }
  
  .btn-hn {
    background-color: #ff4000;
  }
  
  /* Outline Social Share Buttons */
  
  .btn-social-outline {
    background-color: transparent;
    background-image: none;
    text-decoration: none;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out;
  }
  
  .btn-fb-outline {
    color: #3b5998;
    border-color: #3b5998;
  }
  
  .btn-fb-outline:hover,
  .btn-fb-outline:active {
    color: #ffffff;
    background-color: #3b5998 !important;
  }
  
  .btn-tw-outline {
    color: #1da1f2;
    border-color: #1da1f2;
  }
  
  .btn-tw-outline:hover,
  .btn-tw-outline:active {
    color: #ffffff;
    background-color: #1da1f2 !important;
  }
  
  .btn-in-outline {
    color: #0077b5;
    border-color: #0077b5;
  }
  
  .btn-in-outline:hover,
  .btn-in-outline:active {
    color: #ffffff;
    background-color: #0077b5 !important;
  }
  
  .btn-gp-outline {
    color: #db4437;
    border-color: #db4437;
  }
  
  .btn-gp-outline:hover,
  .btn-gp-outline:active {
    color: #ffffff;
    background-color: #db4437 !important;
  }
  
  .btn-rd-outline {
    color: #ff4500;
    border-color: #ff4500;
  }
  
  .btn-rd-outline:hover,
  .btn-rd-outline:active {
    color: #ffffff;
    background-color: #ff4500 !important;
  }
  
  .btn-hn-outline {
    color: #ff4000;
    border-color: #ff4000;
  }
  
  .btn-hn-outline:hover,
  .btn-hn-outline:active {
    color: #ffffff;
    background-color: #ff4000 !important;
  }
  
  /* Fluid Styles */
  
  .fluid {
    display: flex;
  }
  
  .fluid a {
    flex-grow: 1;
    margin-right: 0.25rem;
  }
  
  .fluid a:last-child {
    margin-right: 0rem;
  }


