.optionbox{

  position: relative;
  /* top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); */
}

.optionbox select {
    background: #1146d6;
    color: #fff;
    padding: 10px;
    width: 100%;
    height: 100%;
    border: none;
    font-size: 20px;
    /* box-shadow: 0 5px 48px rgb(93, 15, 9); */
    -webkit-appearance: button;
    outline: none;
}
.optionbox select:focus,
.optionbox select:active {
  background: #1146d6;
  color: #fff;
}
select::-ms-expand {
  display: none !important;
}
select {
  /* for Firefox */
  -moz-appearance: none !important;
  /* for Chrome */
  -webkit-appearance: none !important;
}

/* For IE10 */
select::-ms-expand {
  display: none !important;
}

.optionbox:before{
  content: '\f358';
  font-family: "Font Awesome 5 free";
  position: absolute;
  top: 0px;
  right: 6px;
  height: 100%;
  width: 50px;
  background: #1146d6;
  text-align: center;
  /* line-height: 50px; */
  color: #fff;
  font-size: 37px;
  pointer-events: none;
  line-height: 68px;
}

.optionbox:hover:before{
    background: #0937b8;
  }

















